import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
} from 'reactstrap';
import states from "../api/states";
import { findSetup, setSetup} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";

class FormPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          company : '',
          address : '',
          city:'',
          state:'nn',
          zip:'',
          email:'',
          phone:'',
          fax:'',
          xtoken:'',
          msg: '',
          error: '',
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findSetup(obje_results.user.token).then((response) => {
                this.setState({
                    ...this.state,
                    company: response.data.company,
                    address: response.data.address,
                    city: response.data.city,
                    state: response.data.state,
                    zip: response.data.zip,
                    email: response.data.email,
                    phone: response.data.phone,
                    fax: response.data.fax,
                });
                // console.log('response', response);
            }).catch((respError) => {
                console.log('response error', respError);
            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    handleCompanyInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, company: v,});
        // console.log(v);
    }
    handleAddressInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, address: v,});
    }
    handleCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleZipInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, zip: v,});
    }
    handleEmailInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, email: v,});
    }
    handlePhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, phone: v,});
    }
    handleFaxInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fax: v,});
    }
    handleSaveChanges = () => {
        // console.log('so far', this.state);
        setSetup(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                let newstate = {
                    user : response.udata,
                    is_logged_in : true,
                };
                AsyncStorage.setItem(conf.secret, JSON.stringify(newstate)).then((re) => {})
                .catch((errrr) => {});
                this.setState({
                    ...this.state,
                    msg: response.message + '. Changes will take effect on next login',
                    error: '',
                });

                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    render() {
      return (
        <Page title="Company Setup" breadcrumbs={[{ name: 'Setup', active: true }]}>
          <Row>
            <Col xl={6} lg={12} md={12}>
              <Card>
                <CardHeader>Company information</CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">Company name*</Label>
                      <Input className="long-input" onChange={this.handleCompanyInput}
                        type="text" 
                        value={this.state.company}
                        name="company"
                        placeholder="e.g Greyson LLC"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Address*</Label>
                      <Input className="long-input" onChange={this.handleAddressInput}
                        type="text"
                        value={this.state.address}
                        name="address"
                        placeholder="e.g. 3015 Lanely park"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleUrl">City*</Label>
                      <Input className="long-input" onChange={this.handleCityInput}
                        type="text" 
                        value={this.state.city}
                        name="city"
                        id="city"
                        placeholder="e.g. Plano"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">State*</Label>
                      <Input className="long-input" onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                        <option value="nn" disabled={true}>Select state</option>
                        {
                            states.data.map((stt, idx) => (
                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <br></br>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={6} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleNumber">Zip Code*</Label>
                      <Input className="long-input" onChange={this.handleZipInput}
                        type="number"
                        value={this.state.zip}
                        name="zip"
                        id="zip"
                        placeholder="zip code"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Company email*</Label>
                      <Input className="long-input" onChange={this.handleEmailInput}
                        type="email"
                        value={this.state.email}
                        name="email"
                        placeholder="a.valid@email.com"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Company phone*</Label>
                      <Input className="long-input" onChange={this.handlePhoneInput}
                        type="text"
                        value={this.state.phone}
                        name="phone"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Company fax</Label>
                      <Input className="long-input" onChange={this.handleFaxInput}
                        type="text"
                        value={this.state.fax}
                        name="fax"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Save changes</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default FormPage;
